import React, { useContext, useState } from 'react';
import styled from 'styled-components';
import TopBasicNav from '../../components/common/TopNavBar/TopBasicNav';
import Comment from '../../components/common/Comment';
import { useParams } from 'react-router-dom';
import Post from '../../components/common/Post';
import { useEffect } from 'react';
import Loading from '../../components/common/Loading';
import ContentsLayout from '../../components/layout/ContentsLayout';
import CommentList from './CommentList';
import { UserContext } from '../../context/UserContext';

const Detail = () => {
  const [post, setPost] = useState([]);
  const [comment, setComment] = useState('');
  const [commentList, setCommentList] = useState([]);
  //       state (     )
  const [updateComment, setUpdateComment] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const url = 'https://api.mandarin.weniv.co.kr';
  const { token } = useContext(UserContext);
  const [userImg, setUserImg] = useState('');
  let { id } = useParams();
  const [showCommentList, setShowCommentList] = useState('');
  const [cntCommentList, setCntCommentList] = useState(10); //   

  const [deletedComment, setDeletedComment] = useState(false); //   id 

  /*   */
  useEffect(() => {
    const addUser = () => {
      //     
      if (commentList.length + 10 < cntCommentList) {
        return;
      }
      const scrollHeight = document.documentElement.scrollHeight;
      const scrollTop = document.documentElement.scrollTop;
      const clientHeight = document.documentElement.clientHeight;

      // pc     clientHeight   20 
      if (scrollHeight - scrollTop <= clientHeight + 20) {
        setShowCommentList(commentList.slice(0, cntCommentList));
        setCntCommentList(cntCommentList + 10);
      }
    };

    window.addEventListener('scroll', addUser);

    return () => window.removeEventListener('scroll', addUser);
  }, [cntCommentList]);
  /* * * * * * * * * */

  const getUserProfile = async () => {
    try {
      const req = await fetch(`${url}/user/myInfo`, {
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        method: 'GET',
      });
      const res = await req.json();
      setUserImg(res.user.image);
    } catch (err) {
      console.log(err);
    }
  };

  // title 
  const setTitle = (username) => {
    const titleElement = document.getElementsByTagName('title')[0];
    titleElement.innerHTML = `${username}   |   `;
  };

  const getPostDetail = async () => {
    setIsLoading(true);
    try {
      const req = await fetch(`${url}/post/${id}`, {
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        method: 'GET',
      });

      const res = await req.json();

      // title 
      setTitle(res.post.author.username);

      setPost(res.post);
      setIsLoading(false);
    } catch (err) {
      console.log(err);
      setIsLoading(false);
    }
  };

  const handleCommentSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    const data = {
      comment: {
        content: comment,
      },
    };
    try {
      const req = await fetch(`${url}/post/${id}/comments`, {
        method: 'POST',
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });

      const res = await req.json();
      setUpdateComment(res);
      setCommentList([res.comment, ...commentList]);
      setShowCommentList(
        [res.comment, ...commentList].slice(0, cntCommentList)
      );
      setIsLoading(false);
      setComment('');
      console.log(res);
    } catch (err) {
      console.log(err);
      setIsLoading(false);
    }
  };

  const getCommentList = async () => {
    try {
      const req = await fetch(`${url}/post/${id}/comments/?limit=10000`, {
        method: 'GET',
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
      });
      const res = await req.json();
      setCommentList(res.comments);
      setShowCommentList(res.comments.slice(0, cntCommentList));
      setCntCommentList(cntCommentList + 10);
    } catch (err) {
      console.log(err);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  };

  useEffect(() => {
    getUserProfile();
    getCommentList();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    getPostDetail();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id, updateComment]);

  useEffect(() => {
    const newCommentList = [...commentList];
    for (const i in newCommentList) {
      if (newCommentList[i].id === deletedComment) {
        newCommentList.splice(i, 1);
      }
    }
    setCommentList(newCommentList);
    setShowCommentList(newCommentList.slice(0, cntCommentList));
  }, [deletedComment]);

  return (
    <>
      {isLoading ? (
        <Loading />
      ) : (
        <>
          <TopBasicNav />
          <PostWrapper>
            <h1 className='a11y-hidden'> |   </h1>
            {post && <Post post={post} />}
            <CommentListSection>
              <h3 className='a11y-hidden'> </h3>
              {showCommentList.length > 0 && (
                <>
                  {showCommentList.map((comment, index) => {
                    return showCommentList.length - 1 === index ? (
                      <CommentList
                        key={index}
                        comment={comment}
                        setDeletedComment={setDeletedComment}
                      ></CommentList>
                    ) : (
                      <CommentList
                        key={index}
                        comment={comment}
                        setDeletedComment={setDeletedComment}
                      />
                    );
                  })}
                </>
              )}
            </CommentListSection>
          </PostWrapper>
          <Comment
            txt=''
            placeholder=' '
            value={comment}
            setValue={setComment}
            event={handleCommentSubmit}
          >
            <img src={userImg} alt='  ' />
          </Comment>
        </>
      )}
    </>
  );
};

export default Detail;

const CommentListSection = styled.ul`
  border-top: 1px solid var(--gray-200);
  padding: 20px 16px; //bottom : form  + 20px
  margin: 20px -16px 0;
  display: flex;
  flex-direction: column;
  gap: 16px;
`;

const PostWrapper = styled(ContentsLayout)`
  min-height: 0;
`;
