import React, { useEffect, useState, useContext } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { UserContext } from '../../context/UserContext';
import {
  LANDERS,
  GIANTS,
  WIZ,
  EAGLES,
  LIONS,
  TIGERS,
  TWINS,
  DINOS,
  BEARS,
  HEROES,
} from '../../styles/CommonImages';
import FollowBtn from './FollowBtn';

const UserList = ({ user, keyword }) => {
  const [myTeamImg, setMyTeamImg] = useState('');
  const { myTeam } = useContext(UserContext);

  const myTeamList = [
    { id: '1', name: ' ', name2: 'doosan', img: BEARS },
    { id: '2', name: ' ', name2: 'kiwoom', img: HEROES },
    { id: '3', name: 'LG ', name2: 'lg', img: TWINS },
    { id: '4', name: 'NC ', name2: 'nc', img: DINOS },
    { id: '5', name: 'KIA ', name2: 'kia', img: TIGERS },
    { id: '6', name: ' ', name2: 'samsung', img: LIONS },
    { id: '7', name: 'SSG ', name2: 'ssg', img: LANDERS },
    { id: '8', name: ' ', name2: 'lotte', img: GIANTS },
    { id: '9', name: ' ', name2: 'hanwha', img: EAGLES },
    { id: '10', name: 'KT ', name2: 'kt', img: WIZ },
  ];

  console.log(keyword);

  useEffect(() => {
    function findMyTeam() {
      myTeamList.forEach((item) => {
        if (
          item.name === user.intro?.split('$')[0] ||
          item.name === user.intro?.split('$')[1]
        ) {
          setMyTeamImg(item.img);
        } else if (
          item.name2 === user.intro?.split('$')[1] ||
          item.name === user.intro?.split('$')[1]
        ) {
          setMyTeamImg(item.img);
        }
      });
    }
    findMyTeam();
  }, [user]);

  const matchedText = (text, query) => {
    if (query !== '' && text.includes(query)) {
      const i = text.indexOf(query);
      const parts = [
        text.slice(0, i),
        text.slice(i, i + query.length),
        text.slice(i + query.length),
      ];

      return (
        <>
          {parts[0]}
          <Markedtext myTeam={myTeam}>{parts[1]}</Markedtext>
          {parts[2]}
        </>
      );
    }

    return text;
  };

  return (
    <>
      {user && (
        <UserListItem>
          <Link to={'/profile/' + user.accountname}>
            <img src={user.image} alt='' />
            <div className='user-info'>
              <h2>{matchedText(user.username, keyword)}</h2>
              <p className='ellipsis'>@{user.accountname}</p>
            </div>
          </Link>
          <Container>
            {myTeamImg ? (
              <TeamLogo>
                <img src={myTeamImg} alt='   ' />
              </TeamLogo>
            ) : null}

            <FollowBtn
              profileData={user}
              targetuser={user.accountname}
              isfollow={user.isfollow}
              xsBtn
            />
          </Container>
        </UserListItem>
      )}
    </>
  );
};
export default UserList;

const UserListItem = styled.li`
  display: flex;
  align-items: center;
  justify-content: space-between;

  a {
    display: flex;
    gap: 12px;
  }

  .user-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 6px;
  }

  .user-info h2 {
    font-size: 1.4rem;
    font-weight: bold;
  }

  .user-info p {
    max-width: 264px;
    font-size: 1.2rem;
    color: var(--gray-400);
  }

  img {
    width: 50px;
    aspect-ratio: 1 / 1;
    border-radius: 50%;
    object-fit: cover;
  }
`;

const Container = styled.div`
  display: flex;
  gap: 12px;
  align-items: center;
`;

const TeamLogo = styled.div`
  img {
    width: 40px;
    object-fit: contain;
    border-radius: 0;
  }
`;

const Markedtext = styled.span`
  color: ${(props) =>
    props.myTeam === 'kt'
      ? 'var(--tertiary-color-kt)'
      : 'var(--primary-color-' + (props.myTeam || 'default') + ')'};
`;
