import React, { useContext, useEffect, useState } from 'react';
import UserList from '../../components/common/UserList';
import ContentsLayout from '../../components/layout/ContentsLayout';
import styled from 'styled-components';
import TopTitleNav from '../../components/common/TopNavBar/TopTitleNav';
import { useParams } from 'react-router-dom';
import Loading from '../../components/common/Loading';
import { UserContext } from '../../context/UserContext';

const FollowList = () => {
  const { type, accountname } = useParams();
  const url = 'https://api.mandarin.weniv.co.kr';
  const { token } = useContext(UserContext);
  const [isLoading, setIsLoading] = useState(false);
  const [followList, setFollowList] = useState([]);
  useEffect(() => {
    const titleElement = document.getElementsByTagName('title')[0];
    titleElement.innerHTML = `${
      type === 'follower' ? '' : ''
    } |   `;
  }, []);
  useEffect(() => {
    const getFollowList = async () => {
      setIsLoading(true);
      try {
        const req = await fetch(`${url}/profile/${accountname}/${type}`, {
          headers: {
            Authorization: `Bearer ${token}`,
            'Content-type': 'application/json',
          },
        });
        const res = await req.json();
        setFollowList(res);
        setIsLoading(false);
      } catch (err) {
        console.error(' .', err);
        setIsLoading(false);
      }
    };
    getFollowList();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <TopTitleNav loc={type} />
      <ContentsLayout>
        {isLoading ? (
          <Loading />
        ) : (
          <>
            <UserListWrap>
              {followList &&
                followList.map((user, index) => {
                  return <UserList key={index} user={user}></UserList>;
                })}
            </UserListWrap>
          </>
        )}
      </ContentsLayout>
    </>
  );
};

export default FollowList;

const UserListWrap = styled.ul`
  display: flex;
  flex-direction: column;
  gap: 16px;
`;
