import Form from '../../components/common/Form';
import styled from 'styled-components';
import Button from '../../components/common/Button';
import ShowPasswordBtn from '../../components/common/ShowPasswordBtn';
import { useEffect, useState } from 'react';

const JoinEmail = ({ setPage, email, setEmail, password, setPassword }) => {
  const [isValid, setIsVaild] = useState(false);

  const [messegeEmail, setMessegeEmail] = useState('');
  const [messegePassword, setMessegePassword] = useState('');
  const [isValidEmail, setIsVaildEmail] = useState(false);
  const [isValidPassword, setIsVaildPassword] = useState(false);
  const [showPassword, setShowPassword] = useState(false);

  // ,    ,  
  useEffect(() => {
    if (isValidEmail && isValidPassword) {
      setIsVaild(true);
    } else {
      setIsVaild(false);
    }
  }, [isValidEmail, isValidPassword]);
  const emailvalid = async () => {
    const url = 'https://api.mandarin.weniv.co.kr';
    const reqPath = '/user/emailvalid';

    const userData = {
      user: {
        email: email,
      },
    };
    const reqUrl = url + reqPath;
    const res = await fetch(reqUrl, {
      method: 'POST',
      headers: {
        'Content-type': 'application/json',
      },
      body: JSON.stringify(userData),
    });

    const json = await res.json();

    if (json.message === '   .') {
      setMessegeEmail('');
      setIsVaildEmail(true);
    } else {
      setMessegeEmail(json.message);
      setIsVaildEmail(false);
    }
  };

  const handleForm = (e) => {
    e.preventDefault();
    setPage('joinProfile');
  };

  // email   ,  
  const handleEmailInp = (e) => {
    if (!e.target.value) {
      return;
    }
    if (e.target.validity.patternMismatch) {
      setMessegeEmail('   ');
      setIsVaildEmail(false);
    } else {
      emailvalid(); //api 
    }
  };

  // password 
  // , ,   1  . 6 
  //     
  const reg =
    // eslint-disable-next-line
    /^(?=.*\d+)(?=.*[a-z]+)(?=.*[`~!@#$%^&*\-_=+\[\]\{\}\\\|:;'",<\.>\/?]+)[A-Za-z\d`~!@#$%^&*\-_=+\[\]\{\}\\\|:;'",<\.>\/?]{6,}$/;

  //    
  const handleEmailOnChange = (e) => {
    if (e.target.validity.valueMissing) {
      setMessegeEmail(' ');
      setIsVaildEmail(false);
    } else if (
      messegeEmail === ' ' ||
      !e.target.validity.patternMismatch
    ) {
      setMessegeEmail('');
      setIsVaildEmail(true);
    }
  };
  const handlePasswordOnChange = (e) => {
    // 
    if (!e.target.value) {
      setMessegePassword(' ');
      setIsVaildPassword(false);
      return;
    }
    const patternMatch = reg.test(e.target.value);
    if (patternMatch) {
      setMessegePassword('');
      setIsVaildPassword(true);
    } else {
      setMessegePassword(
        ', ,   6  '
      );
      setIsVaildPassword(false);
    }
  };
  return (
    <StyledJoinEmail>
      <h1> </h1>
      <Form handleForm={handleForm}>
        <label htmlFor='email-inp'></label>
        <input
          id='email-inp'
          type='email'
          onBlur={handleEmailInp}
          value={email}
          pattern='[\w\.\-]+@[a-z]+\.[a-z]{2,}(.?[a-z]+)?'
          placeholder='   .'
          maxLength='98'
          onChange={(e) => {
            setEmail(e.target.value);
            handleEmailOnChange(e);
          }}
          required
          className={messegeEmail && 'invalid'}
        />
        {messegeEmail && <strong>{messegeEmail}</strong>}
        <label htmlFor='password-inp'></label>
        <input
          autoComplete='off'
          id='password-inp'
          type={showPassword ? 'text' : 'password'}
          maxLength='20'
          value={password}
          onChange={(e) => {
            handlePasswordOnChange(e);
            setPassword(e.target.value);
          }}
          className={messegePassword && 'invalid'}
          required
          placeholder='  .'
        />
        <ShowPasswordBtn
          className='show-btn'
          showPassword={showPassword}
          setShowPassword={setShowPassword}
        ></ShowPasswordBtn>
        {messegePassword && <strong>{messegePassword}</strong>}
        <StyledButton
          type='submit'
          bgColor={
            isValid
              ? 'var(--primary-color-default)'
              : 'var(--secondary-color-default)'
          }
          lBtn
          disabled={isValid ? '' : 'disabled'}
        >
          
        </StyledButton>
      </Form>
    </StyledJoinEmail>
  );
};

export default JoinEmail;

const StyledButton = styled(Button)`
  margin: 30px 0 20px;
`;

const StyledJoinEmail = styled.section`
  padding: 30px 34px;
  h1 {
    margin-bottom: 40px;
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 3rem;
    text-align: center;
  }
  a {
    display: block;
    text-align: center;
    font-size: 1.2rem;
    color: var(--gray-400);
  }
  .show-btn {
    position: absolute;
    top: 86px;
    right: -6px;
  }
`;
