import { BASIC_PROFILE_LG, UPLOAD_FILE } from '../../styles/CommonIcons';
import styled from 'styled-components';
import TeamSelect from '../../components/common/Select/TeamSelect';
import Form from '../../components/common/Form';
import Button from '../../components/common/Button';
import UploadModal from '../../components/common/Modal/UploadModal';

import { useEffect, useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { UserContext } from '../../context/UserContext';

const JoinProfile = ({ email, password }) => {
  const navigate = useNavigate();
  const [isValid, setIsVaild] = useState(false);
  const [username, setUsername] = useState('');
  const [accountname, setAccountnameValue] = useState('');
  const [intro, setIntro] = useState('');
  const [image, setImage] = useState('');
  const [src, setSrc] = useState(BASIC_PROFILE_LG);
  const [messageIntro, setMessageIntro] = useState('');
  const [messageAccountname, setMessageAccountname] = useState('');
  const [messageUsername, setMessageUsername] = useState('');
  const [selectedOpt, setSelectedOpt] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [textCnt, setTextCnt] = useState(0);

  const { setAccountname, setMyTeam, setToken } = useContext(UserContext);

  //   ,    true
  const [isVaildIntro, setIsVaildIntro] = useState(true);
  const [isVaildUsername, setIsVaildUsername] = useState(false);
  const [isVaildAccountname, setIsVaildAccountname] = useState(false);

  // CSS    (samsung, ...)
  const teamData = {
    ' ': { team: 'samsung', filename: '1687344208464.png' },
    ' ': { team: 'hanwha', filename: '1687344233670.png' },
    ' ': { team: 'kiwoom', filename: '1687344397365.png' },
    ' ': { team: 'lotte', filename: '1687344422408.png' },
    'LG ': { team: 'lg', filename: '1687344477843.png' },
    'KIA ': { team: 'kia', filename: '1687344489698.png' },
    'SSG ': { team: 'ssg', filename: '1687344502165.png' },
    ' ': { team: 'doosan', filename: '1687344513474.png' },
    'NC ': { team: 'nc', filename: '1687344523820.png' },
    'KT ': { team: 'kt', filename: '1687344531846.png' },
  };

  //  ,  ID  ,  $   
  useEffect(() => {
    if (
      isVaildIntro &&
      isVaildUsername &&
      isVaildAccountname &&
      textCnt <= 150
    ) {
      setIsVaild(true);
    } else {
      setIsVaild(false);
    }
  }, [isVaildIntro, isVaildUsername, isVaildAccountname, textCnt]);

  const url = 'https://api.mandarin.weniv.co.kr';
  const join = async () => {
    const reqPath = '/user';

    const userData = {
      user: {
        username: username,
        email: email,
        password: password,
        accountname: accountname,
      },
    };
    userData.user.intro = intro || '';
    if (selectedOpt && selectedOpt !== '') {
      userData.user.intro += `$${teamData[selectedOpt].team}`;
    }
    if (src !== BASIC_PROFILE_LG) {
      const formData = new FormData();
      formData.append('image', image);
      const reqPath = '/image/uploadfile';
      const reqUrl = url + reqPath;
      const res = await fetch(reqUrl, {
        method: 'POST',
        body: formData,
      });
      const json = await res.json();

      userData.user.image = 'https://api.mandarin.weniv.co.kr/' + json.filename;
    } else {
      //       
      const filename = teamData[selectedOpt]?.filename || '1687309142552.png';
      userData.user.image = 'https://api.mandarin.weniv.co.kr/' + filename;
    }

    const reqUrl = url + reqPath;
    const res = await fetch(reqUrl, {
      method: 'POST',
      headers: {
        'Content-type': 'application/json',
      },
      body: JSON.stringify(userData),
    });

    const json = await res.json();

    if (json.user) {
      login(); //   
    } else {
      alert(json.message);
    }
  };
  const handleForm = (e) => {
    e.preventDefault();
    join();
  };
  // 
  const login = async () => {
    const url = 'https://api.mandarin.weniv.co.kr';
    const reqPath = '/user/login';

    const loginData = {
      user: {
        email: email,
        password: password,
      },
    };
    const reqUrl = url + reqPath;
    const res = await fetch(reqUrl, {
      method: 'POST',
      headers: {
        'Content-type': 'application/json',
      },
      body: JSON.stringify(loginData),
    });

    const json = await res.json();

    if (json.user) {
      const token = json.user['token'];
      const accountname = json.user['accountname'];
      if (!selectedOpt || selectedOpt === '') {
        localStorage.setItem('myteam', '');
        setMyTeam('');
      } else {
        const team = teamData[selectedOpt].team;
        localStorage.setItem('myteam', teamData[selectedOpt].team);
        setMyTeam(team);
      }
      localStorage.setItem('token', token);
      localStorage.setItem('accountname', accountname);
      setAccountname(accountname);
      setToken(token);
      navigate('/'); //   
    } else {
      navigate('/user/login'); //   
    }
  };

  //    
  const handleUsernameInp = (e) => {
    if (e.target.validity.valueMissing) {
      setMessageUsername(' ');
      setIsVaildUsername(false);
    } else if (e.target.validity.tooShort) {
      setMessageUsername('2  ');
      setIsVaildUsername(false);
    } else {
      setMessageUsername('');
      setIsVaildUsername(true);
    }
  };

  // 
  const verifyAccount = async () => {
    const reqPath = '/user/accountnamevalid';
    const reqUrl = url + reqPath;
    const accountData = {
      user: {
        accountname: accountname,
      },
    };
    const res = await fetch(reqUrl, {
      method: 'POST',
      headers: {
        'Content-type': 'application/json',
      },
      body: JSON.stringify(accountData),
    });
    const json = await res.json();
    return json.message;
  };
  //  id  ,  
  const handleAccountnameInp = async (e) => {
    if (e.target.validity.valueMissing) {
      setMessageAccountname(' ');
      setIsVaildAccountname(false);
    } else if (e.target.validity.patternMismatch) {
      setMessageAccountname(', , (.),(_)  .');
      setIsVaildAccountname(false);
    } else {
      setMessageAccountname('');
      setIsVaildAccountname(true);
    }
  };
  //  id    ,  
  const handleAccountnameInpBlur = async (e) => {
    if (!e.target.validity.valid) {
      return;
    }

    const message = await verifyAccount();
    if (message !== '  ID .') {
      setMessageAccountname(message);
      setIsVaildAccountname(false);
    } else {
      setMessageAccountname('');
      setIsVaildAccountname(true);
    }
  };

  //   
  const handleIntroInp = (e) => {
    if (e.target.value.includes('$')) {
      setMessageIntro('($)   ');
      setIsVaildIntro(false);
    } else {
      setMessageIntro('');
      setIsVaildIntro(true);
    }
  };

  //  
  const handleImgDelete = (e) => {
    e.preventDefault();
    setImage('');
    setSrc(BASIC_PROFILE_LG);
    setIsModalOpen(false); //  
  };

  //    textarea height 
  const resizeHeight = (e) => {
    e.target.style.height = 'auto';
    e.target.style.height = e.target.scrollHeight + 'px';
  };
  const handleTextCnt = (e) => {
    setTextCnt(e.target.value.length);
  };
  return (
    <StyledJoinProfile>
      <h1> </h1>
      <p>    .</p>
      <Form handleForm={handleForm}>
        <button
          type='button'
          className='img-btn'
          onClick={(e) => setIsModalOpen(true)}
        >
          <img src={src} alt='' />
          <img className='uplode-img' src={UPLOAD_FILE} alt='' />
        </button>
        {isModalOpen && (
          <UploadModal
            isModalOpen={isModalOpen}
            setIsModalOpen={setIsModalOpen}
          >
            <li>
              <button
                type='button'
                onKeyDown={(e) => {
                  // input focus  
                  if (!e.shiftKey && e.key === 'Tab') {
                    e.preventDefault();
                    e.target.parentNode.nextElementSibling.firstElementChild.focus();
                  }
                }}
                //  , file input click  (   )
                onClick={(e) => {
                  e.target.nextElementSibling.click();
                }}
              >
                 
              </button>
              <input
                type='file'
                id='profile-img'
                className='a11y-hidden'
                onChange={(e) => {
                  if (e.target.files && e.target.files[0]) {
                    const reader = new FileReader();
                    reader.onload = ({ target }) => {
                      setSrc(target.result);
                    };
                    reader.readAsDataURL(e.target.files[0]);
                    setImage(e.target.files[0]);
                  }
                  setIsModalOpen(false); //  
                }}
              />
            </li>
            <li>
              <button type='button' onClick={handleImgDelete}>
                 
              </button>
            </li>
            <li>
              <button type='button' onClick={(e) => setIsModalOpen(false)}>
                
              </button>
            </li>
          </UploadModal>
        )}
        <label htmlFor='username'> </label>
        <input
          id='username'
          type='text'
          placeholder='2~10  .'
          value={username}
          onChange={(e) => {
            setUsername(e.target.value);
            handleUsernameInp(e);
          }}
          minLength={2}
          maxLength={10}
          className={messageUsername && 'invalid'}
          required
        />
        {messageUsername && <strong>{messageUsername}</strong>}
        <label htmlFor='accountname'> ID</label>
        <input
          id='accountname'
          type='text'
          placeholder=', , (.),(_)  .'
          pattern='[A-Za-z0-9\._]+'
          maxLength={16}
          value={accountname}
          onBlur={handleAccountnameInpBlur}
          onChange={(e) => {
            handleAccountnameInp(e);
            setAccountnameValue(e.target.value);
          }}
          className={messageAccountname && 'invalid'}
          required
        />
        {messageAccountname && <strong>{messageAccountname}</strong>}
        <label htmlFor='intro'></label>
        <textarea
          id='intro'
          type='text'
          placeholder='   !'
          value={intro}
          onChange={(e) => {
            handleIntroInp(e);
            setIntro(e.target.value);
            resizeHeight(e);
            handleTextCnt(e);
          }}
          className={messageIntro && 'invalid'}
          maxLength={150}
        />
        <div className={textCnt > 150 ? 'invalid' : ''}>
          <span>{textCnt} / </span>150
        </div>
        {messageIntro && <strong>{messageIntro}</strong>}
        <TeamSelect
          selectedOpt={selectedOpt}
          setSelectedOpt={setSelectedOpt}
        ></TeamSelect>
        <Button
          id='start-btn'
          type='submit'
          bgColor={
            isValid
              ? 'var(--primary-color-default)'
              : 'var(--secondary-color-default)'
          }
          lBtn
          disabled={isValid ? '' : 'disabled'}
        >
             
        </Button>
      </Form>
    </StyledJoinProfile>
  );
};

export default JoinProfile;

const StyledJoinProfile = styled.section`
  padding: 30px 34px;

  h1,
  p {
    text-align: center;
  }
  h1 {
    margin-bottom: 12px;
    font-size: 2.4rem;
    font-weight: 500;
    line-height: 3rem;
  }
  p {
    margin-bottom: 30px;
    font-size: 1.4rem;
    color: var(--gray-400);
  }
  #profile-img {
    border: none;
  }
  .img-btn {
    position: relative;
    display: flex;
    width: 110px;
    aspect-ratio: 1/1;
    margin: 0 auto 30px;
  }
  .img-btn img {
    border-radius: 55px;
    object-fit: cover;
  }
  .img-btn .uplode-img {
    position: absolute;
    width: 36px;
    height: 36px;
    bottom: 0;
    right: 0;
  }
  #my-team {
    margin-top: 9px;
  }
  #start-btn {
    margin-top: 30px;
  }
`;
